.App {
  font-family: Arial, sans-serif;
  text-align: left;
  max-height: 100vh; 
  /* viewport height */
  overflow-y: auto;  /* make it scrollable vertically */
}

/********************************************Manage Worker *****************/

.worker-container-and-side-panel {
  display: flex;
  justify-content: space-between;
  max-height: 850px;
}

.worker-container {
  /* flex: 3; this will take up 3/4 of the parent div's width */
  flex-direction: column;
  flex: 3; /* This will take 3 parts of the available space */
  margin-right: 20px; /* Optional: Add some spacing between the two components */
  /* flex-direction: row; */
}

.worker-details{

  display: flex;
  margin-left: 10px;
  justify-content: space-between;
  flex-direction: row;
  align-items: left;
  width: 1200px;
  text-align: right;
  border-bottom: 1px solid #ccc;
}




.workerColumns {
  background-color: #F4EB27;
  overflow: hidden;
  display: flex;
  justify-content: space-between;
  margin-left: 20px;
  margin-right: 20px;
  width: 1200px;

  /* margin-right: 50px; adjust as needed */
}

.workerColumns a {
  padding: 20px 56px;
  text-decoration: none;
  font-weight: bold;
}

.delete-button {
  background-color: rgb(55, 0, 255);
  color: white;
  margin: 2px;
  border: none;
  
  cursor: pointer;
  border-radius:25px;
  padding: 10px 20px; /* Add some padding */
}

/*****************************Playground*****************/

.progress-bar {
  display: flex;
  gap: 2px;
  max-width: 400px;
  
}

.progress-bar-item {
  flex: 1;
  padding: 1px;
  border: 0px solid #ccc;
  background-color: #f5f5f5;
  text-align: center;
  /* display: table-cell; */
  vertical-align: middle;
    
  /* font-weight: bold; */
  font-size: .8em;
  
}

.progress-bar-item.active {
  background-color: green;
  color: white;
}

/* Adjust font size for medium screens */
@media (max-width: 768px) {
  .progress-bar-item {
    font-size: .6em; /* Adjust as needed */
  }
}

/* Adjust font size for smaller screens */
@media (max-width: 480px) {
  .progress-bar-item {
    font-size: 0.5em; /* Adjust as needed */
  }
}







/**********************************Home view **********************************/



.jobColumns {
  background-color: #F4EB27;
  overflow: hidden;
  display: flex;
  justify-content: space-between;
  margin-left: 10px;
  margin-right: 0px;
  max-width: 1220px;
  /* margin-right: 50px; adjust as needed */
}

.jobColumns p {
  padding: 2px 5px;
  text-decoration: none;
  font-weight: bold;
  margin-left: 10px;
  margin-right: 10px;
}

.jobs-container {
  max-height: 500px;
  flex: 3; /* this will take up 3/4 of the parent div's width */
  flex-direction: column;
  margin-right: 80px;
  overflow-y: auto;
  max-width: 1250px;
}

.jobs-container-and-side-panel {
  display: flex;
  justify-content: space-between;
  max-height: 750px;
}

.job-details p {
  /* width: 1000px; */
  display: flex;
  flex-direction: column;
}

.job-id {
  flex: 0 0 auto;   /* This prevents the job-id from growing or shrinking */
  width: 50px;     /* Adjust as needed */
  text-align: left;
}
/* Adjust font size for pc screens, everything larger than  */
@media (min-width: 768px) {
  .jobs-container {
    /* justify-content: space-between; */
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-end;
    /* margin-right: 20px; */
    margin-right: 10px;
    margin-left: 0px;
    font-size: .8em;
  }
  
  .job-details  {
    justify-content: space-between;
    flex-direction: row;
    align-items: left;
    /* width: 1200px; */
    display: flex;
    text-align: right;
    border-bottom: 1px solid #ccc;
    margin-left: 10px;
    margin-right: 1px;
  }



  .job-details job-id {
    flex: 0 0 auto;   /* This prevents the job-id from growing or shrinking */
    width: 10px;     /* Adjust as needed */
    text-align: left;
    min-width: 20px;
  }
  
  .job-details p {
    flex: 1 1 auto;
    text-align: right;
    min-width: 20px;
    max-width: 100px;
    /* text-align: right; */
  }
}




